<script>
export default {
data: () => ({
    Login: true,
    userData: {
    name: "",
    email: "",
    password: "",
    },
    title: 'Cursos AlfaWeb',
    show: false,
}),


methods: {
    redirect() {
    this.$router.push({ name: "Home" });
    },
    limpiar() {
    this.userData.email = "";
    this.userData.password = "";
    },
    
    async doLogin() {
    try {
        await this.$store.dispatch("user/doLogin", {
        email: this.userData.email,
        password: this.userData.password,
        });
        this.limpiar();
        this.redirect();
    } catch (error) {
        console.error(error.message);
    }
    },
},
};
</script>
