<template>
  <div>
  <v-app-bar fixed app color="deep-purple" dark>
            <v-toolbar-title v-text="title" />
    </v-app-bar>

    <v-container>
      <v-row class="d-flex justify-center mt-6">
        <v-col cols="6">


          <!-- Formulario Register -->        
            <div class="d-flex justify-center py-5">
              <h1>Registrar</h1> 
              <v-spacer></v-spacer>
              <div class="d-flex justify-end mt-2">
                <router-link :to="{ name: 'login' }">
              <v-btn color="#E8EAF6" type="submit"><v-icon>mdi-arrow-u-left-top-bold</v-icon></v-btn>
            </router-link></div>
            </div>
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              d-flex
              justify-center
              @submit.prevent="doRegister"
            >
              <!-- Inputs -->
              <v-text-field
                v-model="userData.email"
                label="E-mail"
                required
                outlined
              ></v-text-field>

              <v-text-field
                v-model="userData.password"
                :type="show ? 'text' : 'password'"
                label="Password"
                outlined
              ></v-text-field>


              <!-- Botones -->
              <div class="d-flex justify-end">
                <v-btn color="error" class="mr-3" rounded @click="limpiar">
                  Limpiar
                </v-btn>

                <v-btn rounded type="submit" color="teal" style="color: white" class="mr-0">
                  Registrar 
                  </v-btn>
              </div>



            </v-form>
          
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>


<script>
export default {
  data: () => ({
    userData: {
      email: "",
      password: "",
    },
    show: false,
    title: 'Cursos AlfaWeb',
  }),


  methods: {
    redirect() {
      this.$router.push({ name: "Home" });
    },
    limpiar() {
    this.userData.email = "";
    this.userData.password = "";
    },

    async doRegister() {
      try {
        await this.$store.dispatch("user/doRegister", {
          email: this.userData.email,
          password: this.userData.password,
        });
        this.limpiar();
        this.redirect();
      } catch (error) {
        console.error(error.message);
      }
    },

    async doLogin() {
      try {
        await this.$store.dispatch("user/doLogin", {
          email: this.userData.email,
          password: this.userData.password,
        });
        this.limpiar();
        this.redirect();
      } catch (error) {
        console.error(error.message);
      }
    },
  },
};
</script>
<style scoped>

router-link a{
    text-decoration: none!important;;
}
</style>